import React, { Component } from "react";
import wrapper_clickFun from "../../components/wrapperClickFun";
import ListPageTable from "../../pages/listPageTable";
const API_form_name = "inwardEntry";
const form_name = "Inward Entry List";
const Create_Update_Path = `/${process.env.REACT_APP_NAME}/${API_form_name}/CreateUpdate`;
const listAPI = `/${process.env.REACT_APP_NAME}/${API_form_name}/ListAPI/`;
const form_type = "Master";

const tableHeaderList = [
    { cn: "Inward No", fv: "code", fn: "inward_no", st: "search_box", sv: "inward_no" },
    { cn: "Applicant", fv: "applicant", fn: "applicant", st: "search_box", sv: "applicant" },
    { cn: "Location", fv: "Location",fn: "loc_desc",st: "search_box",sv: "location__loc_desc",},
    { cn: "Department", fv: "Department",fn: "dep_desc",st: "search_box",sv: "department__dep_desc",},
    { cn: "AWB/Consignment No", fv: "AWB/Consignment No", fn: "awb_consignment_no", st: "search_box", sv: "awb_consignment_no" },
    { cn: "Last Updated By", fv: "User Name", fn: "updated_by", st: "search_box", sv: "updated_by__full_name"},
    { cn: "Last Updated Date", fv: "Date", fn: "updated_date", st: "datepicker", sv: "updated_date" },
    { cn: "Status", fv: "Status", fn: "status", st: "search_box", sv: "status"},
];

class listPage extends Component {
    render() {
        return (
            <div className="wrapper">
                <div className="content-wrapper" onClick={wrapper_clickFun.bind(this)}>
                    <ListPageTable
                        tableHeaderList={tableHeaderList}
                        listAPI={listAPI}
                        to_page={Create_Update_Path}
                        form_name={form_name}
                        form_type={form_type}
                    ></ListPageTable>
                </div>
            </div>
        );
    }
}

export default listPage;
